*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  display: inline-block;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  &::before,
  &::after {
    content: unset;
  }
}

.textArea {
  position: absolute;
}

.bg {
  width: 100%;
}
